* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    font-family: 'DM Sans', sans-serif;
    font-size: 62.5%;
    height: 100%;
    /* background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background: linear-gradient(9deg, rgba(5, 5, 70, 1) 0%, rgba(11, 33, 94, 1) 100%); */
}

main{
    /* min-height: 100vh; */
    display:flex;
    align-items:center;
}

.taskApp{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    color:white;
} 

.taskApp__header{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1024px;
    background-color: #0704DF;
    padding: 30px 0 30px 30px;
    border-radius: 0 0 15px 15px;
}

.taskApp__title{
    font-size: 3rem;
    margin: 0;
    font-weight: 900;
}

.taskApp__search{
    flex-grow: 1;
    padding: 10px;
    width: 90%;
    border-radius: 5px;
    font-family: 'DM Sans', sans-serif;
    font-size: 1rem;
    border-style: none;
    background-color:  transparent;
    color: white;
}

.taskApp__search::placeholder{
    border-bottom: solid 1px white;
    color: #B5B5B5;
}

.taskApp__search:focus{
    outline-color: #0704DF;
    color: white;
}

.taskApp__works{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    gap:2rem;
    max-width: 1024px;
    
}

.taskApp__list{
    width: 95%;
}

.taskApp__items{
    padding: 15px 20px;
    display: flex;
    margin-top: 16px;
    justify-content: space-around;
    font-size: 1rem;
    font-weight: 600;
    align-items: center;
    color: black;
}
.taskApp__tagEmpty{
    color: rgb(185, 185, 185);
    text-align: center;
    font-size: 2rem;
    font-weight: 600;
}

.taskApp__itemsContainer{
    z-index: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: white;
    box-shadow:  3px 3px 10px rgba(110, 110, 110, 0.5);
    flex-grow: 1;
    padding: 10px 10px;
    border-radius: 5px;
    gap: .5rem;
}

.taskApp__check{
    border: 1px solid #B5B5B5;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    cursor: pointer;
}

.taskApp__check-complete{
    background-color: #0704DF;
    border: none;
}

.taskApp__task{
    flex-grow: 1;
}

.taskApp__task-complete{
    text-decoration: line-through;
    color: rgb(173, 173, 173);
}
.taskApp__deleteBtnContainer{
    display: flex;
    width: 30px;
    padding: 3px 0;
    border-radius: 0 5px 5px 0;
    background-color: #0704DF;
    transition: box-shadow .2s ease-in-out;
    transition: background-color .2s ease-in;
    translate: -2px 0;
    transition: translate .2s ease-in-out;
    
}

.taskApp__deleteBtnContainer:hover{
    box-shadow:  1px 1px 10px #7272FA;
    background-color: #0704DF;
    translate : 1px 0
}

.taskApp__deleteIcon{
    margin: auto;
    cursor: pointer;
}

.taskApp__addTask{
    z-index: 1;
    --icon:url(../assets/add-circle-svgrepo-com.png);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    font-family: 'DM Sans', sans-serif;
    font-weight: 900;
    background-image: var(--icon);
    background-size:cover;
    border-style: none;
    background-color:  transparent;
    transition: background-image .2s ease-in-out; 
      
}

.taskApp__addTask:hover{
    --icon:url(../assets/add-circle-svgrepo-com\ hov.png);
    filter: drop-shadow(3px 3px 10px #7272FA); 
}

.taskApp__modalContainer{
    text-align: center;
    background-color: rgba(0, 0, 0, 0.507);
    color: white;
    display:flex;
    align-items:center;
    justify-content: center;
    position: fixed;
    top:0;
    right: 0;
    bottom: 0;
    left: 0;
}

.taskApp__formContainer{
    background-color: rgb(255, 255, 255);
    width: 90%;
    max-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    gap: 20px;
    border-radius: 5px;
    
}

.taskApp__form{
    background-color: #f9fbfc;
    border: 2px solid rgba(34, 34, 34, 0.397);
    border-radius: 2px;
    font-family: 'DM Sans', sans-serif;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, .25);
    color: rgb(34, 34, 34);
    text-align: center;
    font-size: 1rem;
    min-height: 200px;
    width: 100%;
    min-width: 260px;
    max-width: 260px;
}

.taskApp__form:focus{
    outline-color: #0704DF;
}

.taskApp__formButtonContainer{
    display: flex;
    justify-content: center;
    gap: 20px
}

.taskApp__formButton{
    width: 80px;
    padding: 10px 0;
    border-style: none;
    font-family: 'DM Sans', sans-serif;
    background-color: #0704DF;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: filter .2s ease-in-out;
    
}

.taskApp__formButton:hover{
    filter: drop-shadow(3px 3px 10px #7272FA);
}

.taskApp__formButton--cancelar{
    color: rgb(173, 173, 173);
    background-color: transparent;
    box-shadow: none;
    transition: color .2s ease-in-out;
    
}

.taskApp__formButton--cancelar:hover{
   filter: none;
   color: #0704DF;
}
